___CSS_LOADER_EXPORT___.push([module.id, `.contentdivclass {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 10;
  overflow: visible;
}

.legendebuttonclass {
  width: 14px;
  background: #307ac1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.legendekeeperdivclass {
  position: absolute;
  background: silver;
  right: 0px;
  top: 0px;
  bottom: 0px;
  z-index: 20;
  overflow: visible;
  display: flex;
  flex-direction: row;
}

.headerdivclass {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
}

.innercontentdivclass {
  position: absolute;
  left: 0px;
  width: 100%;
  overflow: auto;
}

.urlaubclass {
  width: 38px;
  height: 20px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80%;
}

.homeofficeclass {
  width: 38px;
  height: 36px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80%;
}

.monatedivclass {
  height: 30px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 4px;
  border: 1px solid #444444;
  cursor: pointer;
  color: black;
  background: white;
}

.legendedivclass {
  font-weight: bold;
  color: white;
  width: 30px;
  border: 1px solid #444444;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legendeexterndivclass {
  width: 30px;
  border: 1px solid red;
  height: 20px;
  font-size: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legenderounddivclass {
  font-weight: bold;
  color: white;
  width: 10px;
  border: 1px solid #444444;
  height: 10px;
  border-radius: 50%;
  cursor: pointer;
}

.monatedivclass:hover {
  color: white;
  background: #307ac1;
