  position: relative;
  width: 100%;
  height: 100%;
}

.contentdivclass {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  overflow: auto;
}

.legendebuttonclass {
  width: 14px;
  height: 100%;
  background: #307ac1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.legendekeeperdivclass {
  position: absolute;
  height: 1000px;
  background: silver;
  right: 0px;
  top: 0px;
  z-index: 20;
  overflow: visible;
}

.legendedivclass {
  font-weight: bold;
  font-size: 80%;
  color: white;
  width: 36px;
  border: 1px solid #444444;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legendenewdivclass {
  width: 36px;
  height: 24px;
  display: flex;
  flex-direction: row;
}

.legendeinnerdivclass {
  color: white;
  font-size: 90%;
  width: 50%;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legendebreitdivclass {
  font-weight: bold;
  font-size: 80%;
  color: white;
  width: 60px;
  border: 1px solid #444444;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legendeexterndivclass {
  width: 30px;
  border: 1px solid red;
  height: 20px;
  font-size: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legenderounddivclass {
  font-weight: bold;
  color: white;
  width: 10px;
  border: 1px solid #444444;
  height: 10px;
  border-radius: 50%;
  cursor: pointer;
}

.monatedivclass {
  height: 30px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 4px;
  border: 1px solid #444444;
  cursor: pointer;
  color: black;
  background: white;
}

.monatedivclass:hover {
  color: white;
  background: #307ac1;
